<template>
  <v-card elevation="2" outlined>
    <v-card-title> RF Status </v-card-title>
    <v-card-text>
      <v-container>
        <v-row>        
          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Temperature</v-col>
          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center font-weight-bold'> {{parseFloat(Status.Temperature).toFixed(2)}} &#176;C </v-col>

          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Fan</v-col>
          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center font-weight-bold'> {{Status.FanSpeed}} RPM </v-col>
        </v-row>

        <v-row>
          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Software Mute</v-col>
          
          <v-col align-self="center" cols="2">
            <v-btn-toggle color="primary" mandatory v-model="new_SwEnable">
              <v-btn outlined :value="true" :color="new_SwEnable? 'green' : ''"> 
                Unmute
                <v-icon dark right :color="new_SwEnable? 'green' : ''"> mdi-checkbox-marked-circle </v-icon>
              </v-btn>
              <v-btn outlined :value="false" :color="new_SwEnable == false? 'amber' : ''">
                Mute 
                <v-icon dark right :color="new_SwEnable == false? 'amber' : ''"> mdi-cancel </v-icon>
              </v-btn>
            </v-btn-toggle>
          </v-col>

          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Discrete Mute</v-col>
          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center font-weight-bold green--text'> Unmuted </v-col>
        </v-row>

        <v-row>
          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Step Attenuator</v-col>
          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center font-weight-bold'> {{parseFloat(Status.TxStepAtten).toFixed(2)}} dB</v-col>
          <v-col align-self="center" cols="2" class="text-body-1 font-weight-medium">
            <v-text-field type="number" suffix="dB" dense hide-details
              :step="UnitOption.Tx.StepAtten.Rez" :min="UnitOption.Tx.StepAtten.Min" :max="UnitOption.Tx.StepAtten.Max"
              :rules="[StepAttenIsValid(new_StepAtten)]"
              :label="StepAttenIncrementString" 
              v-model.number="new_StepAtten"> </v-text-field>
          </v-col>

          <v-col cols="2" class="text-body-1 d-flex">
            <v-btn class="mx-1" v-on:click="ChangeAttenuator()">Set Step Atten </v-btn>
          </v-col>
        </v-row>

        <v-row>
          <v-col cols="2" class='text-body-1 d-flex align-center justify-end font-italic'> Power </v-col>
          <v-col cols="2" align-self="center" class='text-body-1 d-flex align-center font-weight-bold'> 
            {{parseFloat(GetTxPower).toFixed(2) + " " + GetPowerUnit}}
          </v-col>

          <v-col cols="2" align-self="center" class="text-body-1 font-weight-medium"> 
            <v-text-field :suffix="GetPowerUnit" dense hide-details
              v-model.number="new_DesiredOutputPower"
              type="number" label="Desired Power"
              step="0.10"
              > </v-text-field>
          </v-col>

          <v-col cols="2" class="text-body-1 d-flex">
            <v-btn class="mx-1" v-on:click="SetDesiredPower()">Set Desired Power </v-btn>
          </v-col>
        </v-row>

        <v-row>
          <v-col cols="2" class='text-body-1 d-flex align-center justify-end font-italic'> Reflected Power </v-col>
          <v-col cols="2" align-self="center" class='text-body-1 d-flex align-center font-weight-bold'> 
            {{parseFloat(GetReflectedPower).toFixed(2) + " " + GetPowerUnit}} 
          </v-col>
        </v-row>
      </v-container>

    </v-card-text>

  </v-card>
</template>


<script>
import utils from '../../utils.js';
import { mapActions } from 'vuex';

const FaultOptions = [
  {type: "None", background: "", textColor: ""},
  {type: "Alarm", background: "amber", textColor: "amber--text"}, 
  {type: "Fault", background: "red", textColor: "red--text"}];


export default {
  name: 'Status',

  props: {

    Status: {
      type: Object,
      default: function() {
        return {
          TxPower: 38.5,
          SwTxEnabled: true,
          TxRefLocked: true,
          TxStepAtten: 25.5,

          Temperature: 55.4,

          FanSpeed: 3490,
          
          Faults: ['Master', 'Drain Voltage', 'Fan Power'],
        }
      }
    },

    ForwardPower: {
      type: Object,
      default: function() {
        return {
          TxPower: 38.5,
          TxStepAtten: 25.5,

          FaultType: 2,
          MinPower: 20.0,
          MaxPower: 42.0,
        }
      }
    },

    UnitOption: {
      type: Object,
      default: function() {
        return {
          Tx: {
            StepAtten: {
              Min: 0.0,
              Max: 31.75,
              Rez: 0.25
            },

            LoFreq: {
              PresetEnabled: true,
              RangeEnabled: false,
              Presets: [28.05, 29.00]
            },

            Alc: {
              Enabled: false,
              Active: false,
            }
          }
        }
      }
    },

    Alias: {
      type: String,
      default: ''
    },

    ReflectiveSettings: {
      type: Object,
      default: function() {
        return {
          Selection: 2,
          Power: 28.0,
          Max: 39.0,
        }
      }
    },

    PowerConversion: {
      type: Number,
      default: 0,
    },

    DesiredPower: {
      type: Number,
      default: 0,
    }
  },

  computed: {
    GetPowerUnit() {
      return utils.GetPowerUnit(this.PowerConversion);
    },

    StepAttenIncrementString() {
      return "Step Increment is " + parseFloat(this.UnitOption.Tx.StepAtten.Rez).toFixed(2) + "dB";
    },

    GetTxPower() {
      return utils.ConversionPowerUnit(this.Status.TxPower, this.PowerConversion);
    },

    GetReflectedPower() {
      return utils.ConversionPowerUnit(this.ReflectiveSettings.Power, this.PowerConversion);
    }
  },

  methods: {
    ...mapActions([
      "PerformSetStepAtten", 
      "PerformSetSwTxEnabled",
      "PerformSetDesiredPower"
    ]),

    dBmToWatt(power) {
      return (1 * Math.pow(10, power / 10)) / 1000.0;
    },
    
    ChangePowerConversion() {
      this.PerformSetPowerConversion(this.new_PowerConversion);
    },

    ChangePowerOnState() {
      this.PerformSetPowerOnState(this.new_BootupRf);
    },

    ChangeSwTxEnable() {
      this.PerformSetSwTxEnabled(this.new_SwEnable);
    },

    StepAttenIsValid(atten) {
      return atten >= this.UnitOption.Tx.StepAtten.Min
        && atten <= this.UnitOption.Tx.StepAtten.Max
        && atten % this.UnitOption.Tx.StepAtten.Rez == 0;
    },

    SetDesiredPower() {
      if (this.new_DesiredOutputPower != this.DesiredPower) {
        this.PerformSetDesiredPower(parseFloat(this.new_DesiredOutputPower));
      }
    },

    ChangeAttenuator() {
      if (this.new_StepAtten != this.StepAtten) {
        this.PerformSetStepAtten(this.new_StepAtten);
      }
    },

  },

  created() {
    this.FaultOptions = FaultOptions;
  },

  data() {
    return {
      
      new_SwEnable: this.Status.SwTxEnabled,
      
      new_DesiredOutputPower: utils.ConvertTo2Decimal(this.DesiredPower),
      new_StepAtten : utils.ConvertTo2Decimal(this.Status.TxStepAtten),
    }
  },

  watch: {
    new_SwEnable: function(value) {
      if (value != this.SwTxEnabled) {
        utils.debounceMethod(this.ChangeSwTxEnable);
      }
    },

    new_StepAtten: function(value) {
      utils.debounceMethod(() => this.new_StepAtten = utils.ConvertTo2Decimal(value));
    },

    new_DesiredOutputPower: function(value) {
      utils.debounceMethod(() => this.new_DesiredOutputPower = utils.ConvertTo2Decimal(value));
    }


  }
  
}
</script>

<style scoped>
.text-field{
  border: none !important;
  background: none !important;
  padding: 0px !important;
  color: rgba(0, 0, 0, 0.6) !important;
}
</style>
