<template>
  <div>
    <v-card elevation="2" outlined>
      <v-card-title> Fault Definitions </v-card-title>
      
      <v-card-text>
        <v-container>
          
          <!-- High RF Power -->
          <v-row>
            <v-col cols="2" class='text-body-1 d-flex align-center justify-end font-italic'> High RF Power </v-col>
            <v-col cols="1" :class="[GetFaultColor(FaultSettings.HighRfOption), 'text-body-1 d-flex align-center justify-end font-weight-bold']">
              {{GetFaultDefinitions(FaultSettings.HighRfOption)}}
            </v-col>
            <v-col cols="3">
              <v-btn-toggle class="ma-2" mandatory color="primary" v-model="new_HighRfOption">
                <div v-for="(option, i) in FaultOptions" :key="i">
                  <v-btn outlined :value="i" :class="option.textColor" :color="option.background">
                    {{option.type}}
                  </v-btn>
                </div>
              </v-btn-toggle>
            </v-col>

            <v-col cols="2" v-if="new_HighRfOption" class='text-body-1 d-flex align-center justify-end font-weight-bold'>
              {{`${ConversionPowerUnit(FaultSettings.HighRfPower)} ${GetPowerUnit}`}}
            </v-col>
            <v-col cols="2" v-if="new_HighRfOption != 0" align-self="center" class="text-body-1 font-weight-medium">
              <v-text-field :suffix="GetPowerUnit" dense hide-details
                v-model.number="new_HighRfPower"
                type="number" label="High RF Power"
                step="0.10"> </v-text-field>
            </v-col>
          </v-row>
          
          <!-- Low RF Power -->
          <v-row>
            <v-col cols="2" class='text-body-1 d-flex align-center justify-end font-italic'> Low RF Power </v-col>
            <v-col cols="1" :class="[GetFaultColor(FaultSettings.LowRfOption), 'text-body-1 d-flex align-center justify-end font-weight-bold']">
              {{GetFaultDefinitions(FaultSettings.LowRfOption)}}
            </v-col>
            <v-col cols="3">
              <v-btn-toggle class="ma-2" mandatory color="primary" v-model="new_LowRfOption">
                <div v-for="(option, i) in FaultOptions" :key="i">
                  <v-btn outlined :value="i" :class="option.textColor" :color="option.background">
                    {{option.type}}
                  </v-btn>
                </div>
              </v-btn-toggle>
            </v-col>

            <v-col v-if="new_LowRfOption" cols="2" class='text-body-1 d-flex align-center justify-end font-weight-bold'>
              {{`${ConversionPowerUnit(FaultSettings.LowRfPower)} ${GetPowerUnit}`}}
            </v-col>
            <v-col cols="2" v-if="new_LowRfOption != 0" align-self="center" class="text-body-1 font-weight-medium">
              <v-text-field :suffix="GetPowerUnit" dense hide-details
                v-model.number="new_LowRfPower"
                type="number" label="Low RF Power"
                step="0.10"
                > </v-text-field>
            </v-col>
            
          </v-row>

          <!-- High Reflected Power -->
          <v-row>
            <v-col cols="2" class='text-body-1 d-flex align-center justify-end font-italic'> High Reflected Power </v-col>
            <v-col cols="1" :class="[GetFaultColor(FaultSettings.ReflectedOption), 'text-body-1 d-flex align-center justify-end font-weight-bold']">
              {{GetFaultDefinitions(FaultSettings.ReflectedOption)}}
            </v-col>
            <v-col cols="3">
              <v-btn-toggle class="ma-2" mandatory color="primary" v-model="new_HighReflectedOption">
                <div v-for="(option, i) in FaultOptions" :key="i">
                  <v-btn outlined :value="i" :class="option.textColor" :color="option.background">
                    {{option.type}}
                  </v-btn>
                </div>
              </v-btn-toggle>
            </v-col>

            <v-col v-if="new_HighReflectedOption" cols="2" class='text-body-1 d-flex align-center justify-end font-weight-bold'>
              {{`${ConversionPowerUnit(FaultSettings.ReflectedPower)} ${GetPowerUnit}`}}
            </v-col>
            <v-col v-if="new_HighReflectedOption != 0" cols="2" align-self="center" class="text-body-1 font-weight-medium">
              <v-text-field :suffix="GetPowerUnit" dense hide-details
                v-model.number="new_HighReflectedPower"
                type="number" label="High Reflected Power"
                step="0.10"
                > </v-text-field>
            </v-col>
          </v-row>

          <!-- Submit Button -->
          <v-row>
            <v-col cols="2" class="text-body-1 d-flex justify-end">
              <v-btn :disabled="IsDifferent == false" v-on:click="SetChanges"> Submit </v-btn> 
            </v-col>
          </v-row>
        </v-container>
      </v-card-text>
    </v-card>

    <v-card elevation="2" outlined>
      <v-card-text>
        <p class="text-h6 text--primary">
          Inhibit

          <v-btn-toggle mandatory color="primary" class="ma-2">
            <v-btn outlined class="primary--text">
              Closed
            </v-btn>
            <v-btn outlined disabled class="primary--text">
              Open
            </v-btn>
          </v-btn-toggle>
        </p>
        
      </v-card-text>
    </v-card>

  </div>  
</template>


<script>

import utils from '../../utils.js'
import { mapActions } from 'vuex';

const FaultOptions = [
  {type: "Off", background: "", textColor: ""},
  {type: "Alarm", background: "amber", textColor: "amber--text"},
  {type: "Fault", background: "red", textColor: "red--text"}];

export default {
  name: 'RfSettings',

  filters: {
    checkPowerInput: function(value) {
      if (!value) 
        return 0;
      const num = (value * 100 / 10);
      const remainder = Math.round(value * 100 % 10);
      return parseFloat((num + remainder) / 100.0).toFixed(2);
    }
  },
  
  props: {
    FaultSettings: {
      type: Object,
      default: function() {
        return {
          HighRfPower:41.2,
          HighRfOption: 2,

          LowRfPower: 21.5,
          LowRfOption: 2,

          ReflectedPower: 35.6,
          ReflectedOption: 2
        };
      }
    },
    
    PowerConversion: {
      type: Number,
      default: 0
    }
  },

  created() {
    this.FaultOptions = FaultOptions;
  },

  computed: {
    GetPowerUnit() {
      return utils.GetPowerUnit(this.PowerConversion);
    },

    IsDifferent() {
      return this.FaultSettings.HighRfPower != parseFloat(this.new_HighRfPower)
        || this.FaultSettings.HighRfOption != this.new_HighRfOption

        || this.FaultSettings.LowRfPower != parseFloat(this.new_LowRfPower)
        || this.FaultSettings.LowRfOption != this.new_LowRfOption

        || this.FaultSettings.ReflectedPower != parseFloat(this.new_HighReflectedPower)
        || this.FaultSettings.ReflectedOption != this.new_HighReflectedOption;
    }
  },

  methods: {
    ...mapActions(["PerformSetFaultDefinitions"]),

    GetFaultDefinitions(selection) {
      return utils.GetFaultDefinition(selection);
    },

    ConversionPowerUnit(power) {
      return utils.ConversionPowerUnit(power, this.PowerConversion);
    },

    GetFaultColor(selection) {
      return FaultOptions[selection].textColor;
    },

    SetChanges() {
      const newFaults = {
        HighRfPower: parseFloat(this.new_HighRfPower),
        HighRfOption: this.new_HighRfOption,

        LowRfPower: parseFloat(this.new_LowRfPower),
        LowRfOption: this.new_LowRfOption,

        ReflectedPower: parseFloat(this.new_HighReflectedPower),
        ReflectedOption: this.new_HighReflectedOption
      }
      this.PerformSetFaultDefinitions(newFaults);
    }
    
  },

  watch: {
    PowerConversion: function() {
      // this.new_HighRfPower = this.ConversionPowerUnit(this.FaultSettings.HighRfPower);
      // this.new_LowRfPower = this.ConversionPowerUnit(this.FaultSettings.LowRfPower);
      // this.new_HighReflectedPower = this.ConversionPowerUnit(this.FaultSettings.ReflectedPower);
    },

    new_HighRfPower: function(value) {
      utils.debounceMethod(() => this.new_HighRfPower = this.ConversionPowerUnit(value));
    },
    new_LowRfPower: function(value) {
      utils.debounceMethod(() => this.new_LowRfPower = this.ConversionPowerUnit(value)); 
    },
    new_HighReflectedPower: function(value) {
      utils.debounceMethod(() => this.new_HighReflectedPower = this.ConversionPowerUnit(value)); 
    },

  },

  data() {
    return {
      new_HighRfOption: this.FaultSettings.HighRfOption,
      new_HighRfPower : this.ConversionPowerUnit(this.FaultSettings.HighRfPower),

      new_LowRfOption: this.FaultSettings.LowRfOption,
      new_LowRfPower : this.ConversionPowerUnit(this.FaultSettings.LowRfPower),

      new_HighReflectedOption: this.FaultSettings.ReflectedOption,
      new_HighReflectedPower: this.ConversionPowerUnit(this.FaultSettings.ReflectedPower),
    }
  }


}
</script>

<style scoped>
.input-field {
  padding: 5px;
  margin: 5px;
}
</style>