<script>

import utils from '../../utils.js'

export default {
  name: 'ImportOptions',

  props: {
    Filename: {
      type: File,
      default: null
    },

    UnitSettings: {
      type: Object,
      default: () => {
        return {
          Alias: "N/A",
          PowerOnState: false,
          PowerUnit: 0
        };
      }
    },

    FaultDefinitions: {
      type: Object,
      default: () => {
        return {
          HighRfPower: 41.2,
          HighRfOption: 2,

          LowRfPower: 21.5,
          LowRfOption: 2,

          ReflectedPower: 35.6,
          ReflectedOption: 2,
        }
      }
    },

    NtpOptions: {
      type: Object,
      default: () => {
        return {
          PrimaryAddr: "192.168.100.10",
          SecondaryAddr: "192.168.200.10",
          IsManual: true,
          ManualTime: "2021-01-12T12:30:00",
          UpdateDaily: false,
          UtcOffsetMinute: 80,
        }
      }
    },

    SerialOptions: {
      type: Object,
      default: () => {
        return {
          IsRs485: true,
          SelectedBaudrate: 115200
        }
      }
    },

    AlcOptions: {
      type: Object,
      default: () => {
        return {
          SetPoint: 32.1,
          HighAlarm: 30.0,
          LowAlarm: 20.2,
          HighFault: 40.0,
          LowFault: 40.0,
        };
      }
    },

    IpConfigurations: {
      type: Object,
      default: () => {
        return {
          IsDhcp: true,
          Address: "192.168.1.100",
          Subnet: "255.255.255.0",
          Gateway: "192.168.1.1"
        }
      },
    }
  },

  data() {
    return {
      ErrorMessage: "",
      ipCheckbox: false,

      unitSettings: null,
      faultDefinitions: null,
      ntpOptions: null,
      serialOptions: null,
      alcOptions: null,
      ipConfigurations: null,
    };
  },

  methods: {
    async ParseFile(filename) {
      if (filename != null) {
        this.ErrorMessage = "";
        let content = await utils.ReadFileAsync(this.Filename);
        try {
          var newParams = JSON.parse(content);
          
          this.unitSettings = newParams.UnitSettings;
          this.faultDefinitions = newParams.FaultDefinitions;
          this.ntpOptions = newParams.NtpOptions;
          this.serialOptions = newParams.SerialOptions;
          this.alcOptions = newParams.AlcOptions;
          this.ipConfigurations = newParams.IpConfigurations;
        }
        catch (e) {
          console.log(e);
          this.ErrorMessage = "Failed To Parse File!";  
        }
        
      }
      else {
        this.ErrorMessage = "File Does Not Exist!";
      }
    },

    GetFaultDefinition(selection) {
      return utils.GetFaultDefinition(selection);
    },

    ImportConfigsToUnit() {

    }
  },

  computed: {
    GetPowerUnit() {
      return utils.GetPowerUnit(this.UnitSettings.PowerUnit);
    },
    getPowerUnit() {
      return utils.GetPowerUnit(this.unitSettings.PowerUnit);
    }
  },

  created() {
    this.ParseFile(this.Filename);
  },

  watch: {
    Filename () {
      this.ParseFile(this.Filename);
    },
  }

  
}
</script>
