import _ from 'lodash';

export default {

  debounceMethod: _.debounce(function(method) {
    method();
  }, 750),

  ConversionPowerUnit: function(power, conversion) {
    var calculatedPower = power;
    switch (conversion) {
      // dBw
      case 1:
        calculatedPower = power - 30.0;
        break;
      
      // Watt
      case 2:
        calculatedPower = (1 * Math.pow(10, power / 10)) / 1000.0;        
        break;
    }
    return parseFloat(calculatedPower).toFixed(2);
  },

  GetPowerUnit: function(conversion) {
    switch (conversion) {
      case 1:
        return "dBw";
      
      case 2:
        return "W";

      default:
        return "dBm";
    }
  },

  GetFaultDefinition: function(selection) {
    switch (selection) {
      case 1:
        return "Alarm";

      case 2:
        return "Fault";

      default:
        return "None";
    }
  },

  ReadFileAsync(filename) {
    return new Promise((resolve, reject) => {
      let reader = new FileReader();

      reader.onloadend = function(e) {
        resolve(e.target.result);
      };

      reader.onerror = function(e) {
        reject(e);
      };

      reader.readAsText(filename);
    });
  },

  ConvertTo2Decimal(number) {
    return parseFloat(number).toFixed(2);
  },

  IsValidIp(addr) {
    var ipformat = /^(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)$/;
    if (addr.match(ipformat)) {
      return true;
    }
    return false;
  },

}