<template>
  <v-card elevation="2" outlined>
    <v-card-title>ALC</v-card-title>
    <v-card-text>
      <v-container>
        <v-row>
          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>ALC Status</v-col>
          <v-col align-self="center" cols="2" :class="[AlcSettings.IsEnabled? 'green--text' : 'amber--text', 'text-body-1 d-flex align-center font-weight-bold']">
            {{AlcSettings.IsEnabled ? "Enabled" : "Disabled"}}
          </v-col>

          <v-col cols="2" align-self="center" >
            <v-btn-toggle color="primary" mandatory v-model="new_AlcState">
              <v-btn outlined :value="true" :color="new_AlcState? 'green' : ''">
                Enable
                <v-icon dark right :color="new_AlcState? 'green' : ''"> mdi-checkbox-marked-circle </v-icon>
              </v-btn>
              <v-btn outlined :value="false" :color="new_AlcState == false? 'amber' : ''">
                Disable
                <v-icon dark right :color="new_AlcState == false? 'amber' : ''"> mdi-cancel </v-icon>
              </v-btn>
            </v-btn-toggle>
          </v-col>
        </v-row>

        <v-row>
          <v-col cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>ALC Setpoint </v-col>
          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center font-weight-bold'> 
            {{`${ConversionPowerUnit(AlcSettings.SetPoint)} ${GetPowerUnit}`}}
          </v-col> 
          <v-col cols="2" align-self="center" class="text-body-1 font-weight-medium">
            <v-text-field :suffix="GetPowerUnit" dense hide-details
              v-model.number="new_setpoint"
              type="number" label="Output Alarm"
              step="0.10"> </v-text-field>
          </v-col>
        </v-row>
        
        <v-row>
          <v-col cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>High Output Warning</v-col>
          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center font-weight-bold'> 
            {{`${ConversionPowerUnit(AlcSettings.HighAlarm)} ${GetPowerUnit}`}}
          </v-col> 
          <v-col cols="2" align-self="center" class="text-body-1 font-weight-medium">
            <v-text-field :suffix="GetPowerUnit" dense hide-details
              v-model.number="new_highAlarm"
              type="number" label="Output Alarm"
              step="0.10"> </v-text-field>
          </v-col>

          <v-col cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Low Output Warning</v-col>
          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center font-weight-bold'> 
            {{`${ConversionPowerUnit(AlcSettings.LowAlarm)} ${GetPowerUnit}`}}
          </v-col> 
          <v-col cols="2" align-self="center" class="text-body-1 font-weight-medium">
            <v-text-field :suffix="GetPowerUnit" dense hide-details
              v-model.number="new_lowAlarm"
              type="number" label="Output Alarm"
              step="0.10"> </v-text-field>
          </v-col>
        </v-row>

        <v-row>
          <v-col cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>High Output Fault</v-col>
          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center font-weight-bold'> 
            {{`${ConversionPowerUnit(AlcSettings.HighFault)} ${GetPowerUnit}`}}
          </v-col> 
          <v-col cols="2" align-self="center" class="text-body-1 font-weight-medium">
            <v-text-field :suffix="GetPowerUnit" dense hide-details
              v-model.number="new_highFault"
              type="number" label="Output Fault"
              step="0.10"> </v-text-field>
          </v-col>

          <v-col cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Low Output Fault</v-col>
          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center font-weight-bold'> 
            {{`${ConversionPowerUnit(AlcSettings.LowFault)} ${GetPowerUnit}`}}
          </v-col> 
          <v-col cols="2" align-self="center" class="text-body-1 font-weight-medium">
            <v-text-field :suffix="GetPowerUnit" dense hide-details
              v-model.number="new_lowFault"
              type="number" label="Output Fault"
              step="0.10"> </v-text-field>
          </v-col>
        </v-row>

        <v-row>
          <v-col cols="2" class="text-body-1 d-flex justify-end">
            <v-btn class="mx-1" :disabled="IsDifferent == false">Submit </v-btn>
          </v-col>
        </v-row>

      </v-container>
      
    </v-card-text>
  </v-card>

</template>

<script>

import utils from '../../utils.js'

export default {
  name: 'AlcSettings',

  props: {

    AlcSettings: {
      type: Object,
      default: function() {
        return {
          SetPoint: 32.1,

          HighAlarm: 30.0,
          LowAlarm: 20.2,

          HighFault: 40.0,
          LowFault: 40.0,

          IsEnabled: false,
        }
      }
    },

    PowerConversion: {
      type: Number,
      default: 0,
    },
  },

  data() {
    return {
      new_setpoint : this.ConversionPowerUnit(this.AlcSettings.SetPoint),
      new_highAlarm : this.ConversionPowerUnit(this.AlcSettings.HighAlarm),
      new_lowAlarm : this.ConversionPowerUnit(this.AlcSettings.LowAlarm),

      new_highFault : this.ConversionPowerUnit(this.AlcSettings.HighFault),
      new_lowFault : this.ConversionPowerUnit(this.AlcSettings.LowFault),

      new_AlcState: this.AlcSettings.IsEnabled,
    }
  },

  computed: {
    IsDifferent() {
      const result= this.new_AlcState != this.AlcSettings.IsEnabled
        || this.new_setpoint != this.AlcSettings.SetPoint

        || this.new_highAlarm != this.AlcSettings.HighAlarm
        || this.new_lowAlarm!= this.AlcSettings.LowAlarm

        || this.new_highFault!= this.AlcSettings.HighFault
        || this.new_lowFault!= this.AlcSettings.LowFault;
      return result;
    },

    GetPowerUnit() {
      return utils.GetPowerUnit(this.PowerConversion);
    },
  },

  methods: {
    ConversionPowerUnit(power) {
      return utils.ConversionPowerUnit(power, this.PowerConversion);
    },
  },

  watch: {
    PowerConversion: function() {

      // this.new_setpoint = this.ConversionPowerUnit(this.AlcSettings.SetPoint);

      // this.new_highAlarm = this.ConversionPowerUnit(this.AlcSettings.HighAlarm);
      // this.new_lowAlarm = this.ConversionPowerUnit(this.AlcSettings.LowAlarm);

      // this.new_highFault = this.ConversionPowerUnit(this.AlcSettings.HighFault);
      // this.new_lowFault = this.ConversionPowerUnit(this.AlcSettings.LowFault);

      // this.new_AlcState = this.AlcSettings.IsEnabled;
    },

    new_setpoint: function(value) {
      utils.debounceMethod(() => this.new_setpoint = this.ConversionPowerUnit(value));
    },
    
    new_highAlarm: function(value) {
      utils.debounceMethod(() => this.new_highAlarm = this.ConversionPowerUnit(value));
    },

    new_lowAlarm: function(value) {
      utils.debounceMethod(() => this.new_lowAlarm = this.ConversionPowerUnit(value));
    },

    new_highFault: function(value) {
      utils.debounceMethod(() => this.new_highFault = this.ConversionPowerUnit(value));
    },

    new_lowFault: function(value) {
      utils.debounceMethod(() => this.new_lowFault = this.ConversionPowerUnit(value));
    },
  }
}
</script>