<template>
  <v-card elevation="2">
    <v-card-title> Export/Import </v-card-title>
    <v-card-text>

      <v-container>
        <!-- Export Settings -->
        <v-btn class="mx-1" @click.stop="ExportDialog = true"> Export Settings </v-btn>
        <v-dialog v-model="ExportDialog">
          <export-options
            v-bind:UnitSettings="unitSettings"
            v-bind:FaultDefinitions="FaultDefinitions"
            v-bind:NtpOptions="NtpOptions"
            v-bind:SerialOptions="SerialOptions"
            v-bind:AlcOptions="AlcOptions"
            v-bind:IpConfigurations="IpConfigurations"></export-options>
        </v-dialog>

        <!-- Import Settings -->
        <v-btn class="mx-1" v-on:click="importFunction()"> Import Settings </v-btn>
        <v-file-input id="configFileInput" v-model="newFilename" style="display: none;"> </v-file-input>
        <v-dialog v-model="ImportDialog">
          <import-options 
            v-bind:Filename="newFilename"
            v-bind:UnitSettings="unitSettings"
            v-bind:FaultDefinitions="FaultDefinitions"
            v-bind:NtpOptions="NtpOptions"
            v-bind:SerialOptions="SerialOptions"
            v-bind:AlcOptions="AlcOptions"
            v-bind:IpConfigurations="IpConfigurations"></import-options>
        </v-dialog>
      </v-container>

    </v-card-text>
  </v-card>
</template>

<script>
import ExportOptions from './ExportOptions.vue'
import ImportOptions from './ImportOptions.vue'

export default {
  name: 'ImportExport',

  components: {
    ExportOptions,
    ImportOptions,
  },

  props: {
    Alias: {
      type: String,
      default: "",
    },

    PowerOnRf: {
      type: Boolean,
      default: false,
    },

    PowerUnit: {
      type: Number,
      default: 0
    },

    FaultDefinitions: {
      type: Object,
      default: () => {
        return {
          HighRfPower: 41.2,
          HighRfOption: 2,

          LowRfPower: 21.5,
          LowRfOption: 2,

          ReflectedPower: 35.6,
          ReflectedOption: 2,
        }
      }
    },

    NtpOptions: {
      type: Object,
      default: () => {
        return {
          PrimaryAddr: "192.168.100.10",
          SecondaryAddr: "192.168.200.10",
          IsManual: true,
          ManualTime: "2021-01-12T12:30:00",
          UpdateDaily: false,
          UtcOffsetMinute: 80,
        }
      }
    },

    SerialOptions: {
      type: Object,
      default: () => {
        return {
          IsRs485: true,
          SelectedBaudrate: 115200
        }
      }
    },

    AlcOptions: {
      type: Object,
      default: () => {
        return {
          SetPoint: 32.1,
          HighAlarm: 30.0,
          LowAlarm: 20.2,
          HighFault: 40.0,
          LowFault: 40.0,
        };
      }
    },

    IpConfigurations: {
      type: Object,
      default: () => {
        return {
          IsDhcp: true,
          Address: "192.168.1.100",
          Subnet: "255.255.255.0",
          Gateway: "192.168.1.1"
        }
      },
    }
  },

  data() {
    return {
      ImportDialog: false,
      ExportDialog: false,
      IsExport: false,

      newFilename: undefined,

      unitSettings: {
        Alias: this.Alias,
        PowerOnState: this.PowerOnRf,
        PowerUnit: this.PowerUnit
      }
    }
  },

  methods: {
    importFunction() {
      document.getElementById('configFileInput').click();
    }
  },

  watch: {
    newFilename() {
      if (this.newFilename != undefined) {
        this.ImportDialog = true;
      }
    },

    ImportDialog() {
      if (this.ImportDialog == false) {
        this.newFilename = undefined;
      }
    },

    Alias() {
      this.unitSettings = {
        Alias: this.Alias,
        PowerOnState: this.PowerOnRf,
        PowerUnit: this.PowerUnit
      }
    },

    PowerOnRf() {
      this.unitSettings = {
        Alias: this.Alias,
        PowerOnState: this.PowerOnRf,
        PowerUnit: this.PowerUnit
      }
    },

    PowerUnit() {
      this.unitSettings = {
        Alias: this.Alias,
        PowerOnState: this.PowerOnRf,
        PowerUnit: this.PowerUnit
      }
    },

  }

}

</script>