<template>
  <v-card elevation="2">
    <v-toolbar color="grey" dark outlined>
      <v-card-title> Export Options </v-card-title>
    </v-toolbar>

    <v-card-text>
      <v-container>
        
        <!-- Unit Settings -->
        <v-card outlined class="pb-2" v-if="UnitSettings != null">
          <v-card-title> Unit Settings </v-card-title>
          <v-row> 
            <v-col cols="2" class='text-body-1 d-flex justify-end align-center font-italic'>
              Alias
            </v-col>
            <v-col cols="4" class='text-body-1 d-flex justify-start font-weight-bold'>
              {{UnitSettings.Alias != null && UnitSettings.Alias.length > 0 ? UnitSettings.Alias : 'N/A'}}
            </v-col>

            <v-col cols="2" class='text-body-1 d-flex justify-end align-center font-italic'>
              Power-On RF State
            </v-col>
            <v-col cols="4" class='text-body-1 d-flex justify-start font-weight-bold'>
              {{UnitSettings.PowerOnState ? "Enabled" : "Disabled"}}
            </v-col>
              
          </v-row>

          <v-row> 
            <v-col cols="2" class='text-body-1 d-flex justify-end align-center font-italic'>
              Power Unit
            </v-col>
            <v-col cols="4" class='text-body-1 d-flex justify-start font-weight-bold'>
              {{GetPowerUnit}}
              <!-- {{UnitSettings.PowerUnit == 2 ? "W" : (UnitSettings.PowerUnit == 1 ? "dBw" : "dBm")}} -->
            </v-col>
          </v-row>
        </v-card>

        <!-- Fault Definitions -->
        <v-card outlined class="pb-2" v-if="FaultDefinitions != null">
          <v-card-title> Fault Definitions </v-card-title>
          <v-row> 
            <v-col cols="2" class='text-body-1 d-flex justify-end align-center font-italic'>
              High RF Power
            </v-col>
            <v-col cols="1" class='text-body-1 d-flex justify-start font-weight-bold'>
              {{GetFaultDefinition(FaultDefinitions.HighRfOption)}}
            </v-col>

            <v-col cols="2" class='text-body-1 d-flex justify-start font-weight-bold'>
              {{`${parseFloat(FaultDefinitions.HighRfPower).toFixed(2)} ${GetPowerUnit}`}}
            </v-col>
          </v-row>

          <v-row> 
            <v-col cols="2" class='text-body-1 d-flex justify-end align-center font-italic'>
              Low RF Power
            </v-col>
            <v-col cols="1" class='text-body-1 d-flex justify-start font-weight-bold'>
              {{GetFaultDefinition(FaultDefinitions.LowRfOption)}}
            </v-col>

            <v-col cols="2" class='text-body-1 d-flex justify-start font-weight-bold'>
              {{`${parseFloat(FaultDefinitions.LowRfPower).toFixed(2)} ${GetPowerUnit}`}}
            </v-col>
          </v-row>

          <v-row> 
            <v-col cols="2" class='text-body-1 d-flex justify-end align-center font-italic'>
              Reflected RF Power
            </v-col>
            <v-col cols="1" class='text-body-1 d-flex justify-start font-weight-bold'>
              {{GetFaultDefinition(FaultDefinitions.ReflectedOption)}}
            </v-col>
            <v-col cols="2" class='text-body-1 d-flex justify-start font-weight-bold'>
              {{`${parseFloat(FaultDefinitions.ReflectedPower).toFixed(2)} ${GetPowerUnit}`}}
            </v-col>
          </v-row>
        </v-card>

        <!-- Clock NTP -->
        <v-card outlined class="pb-2" v-if="NtpOptions != null">
          <v-card-title> Clock (NTP) </v-card-title>

          <v-row> 
            <v-col cols="2" class='text-body-1 d-flex justify-end align-center font-italic'>
              NTP Server
            </v-col>
            <v-col cols="4" class='text-body-1 d-flex justify-start font-weight-bold'>
              {{NtpOptions.IsManual ? "Manual" : "NTP Server"}}
            </v-col>

            <v-col cols="2" class='text-body-1 d-flex justify-end align-center font-italic'>
              Entered Time
            </v-col>
            <v-col cols="4" class='text-body-1 d-flex justify-start font-weight-bold'>
              {{NtpOptions.ManualDateTime}}
            </v-col>
          </v-row>

          <v-row> 
            <v-col cols="2" class='text-body-1 d-flex justify-end align-center font-italic'>
              UTC Offset
            </v-col>
            <v-col cols="4" class='text-body-1 d-flex justify-start font-weight-bold'>
              {{NtpOptions.UtcOffsetMinute}}
            </v-col>
            
            <v-col cols="2" class='text-body-1 d-flex justify-end align-center font-italic'>
              Update Daily
            </v-col>
            <v-col cols="4" class='text-body-1 d-flex justify-start font-weight-bold'>
              {{NtpOptions.UpdateDaily}}
            </v-col>
          </v-row>

          <v-row> 
            <v-col cols="2" class='text-body-1 d-flex justify-end align-center font-italic'>
              Primary Address
            </v-col>
            <v-col cols="4" class='text-body-1 d-flex justify-start font-weight-bold'>
              {{NtpOptions.PrimaryAddr}}
            </v-col>
            
            <v-col cols="2" class='text-body-1 d-flex justify-end align-center font-italic'>
              Secondary Address
            </v-col>
            <v-col cols="4" class='text-body-1 d-flex justify-start font-weight-bold'>
              {{NtpOptions.SecondaryAddr}}
            </v-col>
          </v-row>
        </v-card>

        <!-- Serial Options -->
        <v-card outlined class="pb-2" v-if="SerialOptions != null">
          <v-card-title>
            Serial Interface
          </v-card-title>

          <v-row> 
            <v-col cols="2" class='text-body-1 d-flex justify-end align-center font-italic'>
              Interface
            </v-col>
            <v-col cols="4" class='text-body-1 d-flex justify-start font-weight-bold'>
              {{SerialOptions.IsRs485 ? "RS-485" : "RS-232"}}
            </v-col>
            
            <v-col cols="2" class='text-body-1 d-flex justify-end align-center font-italic'>
              Baudrate
            </v-col>
            <v-col cols="4" class='text-body-1 d-flex justify-start font-weight-bold'>
              {{SerialOptions.SelectedBaudrate}}
            </v-col>
          </v-row>
        </v-card>

        <!-- ALC Options -->
        <v-card outlined class="pb-2" v-if="AlcOptions != null">
          <v-card-title>
            ALC Settings
          </v-card-title>

          <v-row> 
            <v-col cols="2" class='text-body-1 d-flex justify-end align-center font-italic'>
              ALC Setpoint
            </v-col>
            <v-col cols="4" class='text-body-1 d-flex justify-start font-weight-bold'>
              {{`${parseFloat(AlcOptions.SetPoint).toFixed(2)} ${GetPowerUnit}`}}
            </v-col>
          </v-row>

          <v-row> 
            <v-col cols="2" class='text-body-1 d-flex justify-end align-center font-italic'>
              High Output Warning
            </v-col>
            <v-col cols="4" class='text-body-1 d-flex justify-start font-weight-bold'>
              {{`${parseFloat(AlcOptions.HighAlarm).toFixed(2)} ${GetPowerUnit}`}}
            </v-col>
            
            <v-col cols="2" class='text-body-1 d-flex justify-end align-center font-italic'>
              Low Output Warning
            </v-col>
            <v-col cols="4" class='text-body-1 d-flex justify-start font-weight-bold'>
              {{`${parseFloat(AlcOptions.LowAlarm).toFixed(2)} ${GetPowerUnit}`}}
            </v-col>
          </v-row>

          <v-row> 
            <v-col cols="2" class='text-body-1 d-flex justify-end align-center font-italic'>
              High Output Fault
            </v-col>
            <v-col cols="4" class='text-body-1 d-flex justify-start font-weight-bold'>
              {{`${parseFloat(AlcOptions.HighFault).toFixed(2)} ${GetPowerUnit}`}}
            </v-col>

            <v-col cols="2" class='text-body-1 d-flex justify-end align-center font-italic'>
              Low Output Fault
            </v-col>
            <v-col cols="4" class='text-body-1 d-flex justify-start font-weight-bold'>
              {{`${parseFloat(AlcOptions.LowFault).toFixed(2)} ${GetPowerUnit}`}}
            </v-col>
          </v-row>
        </v-card>

        <!-- IP Configurations -->
        <v-card outlined class="pb-2" v-if="IpConfigurations != null">
          <v-card-title>
            IP Configuration
          </v-card-title>

          <v-row> 
            <v-col cols="2" class='text-body-1 d-flex justify-end align-center font-italic'>
              DHCP State
            </v-col>
            <v-col cols="4" class='text-body-1 d-flex justify-start font-weight-bold'>
              {{IpConfigurations.IsDhcp? "DHCP" : "Static"}}
            </v-col>
            
            <v-col cols="2" class='text-body-1 d-flex justify-end align-center font-italic'>
              Subnet
            </v-col>
            <v-col cols="4" class='text-body-1 d-flex justify-start font-weight-bold'>
              {{IpConfigurations.Subnet}}
            </v-col>
          </v-row>

          <v-row>
            <v-col cols="2" class='text-body-1 d-flex justify-end align-center font-italic'>
              Address
            </v-col>
            <v-col cols="4" class='text-body-1 d-flex justify-start font-weight-bold'>
              {{IpConfigurations.Address}}
            </v-col>
            
            <v-col cols="2" class='text-body-1 d-flex justify-end align-center font-italic'>
              Gateway
            </v-col>
            <v-col cols="4" class='text-body-1 d-flex justify-start font-weight-bold'>
              {{IpConfigurations.Gateway}}
            </v-col>
          </v-row>
        </v-card>

        <v-btn class="ma-1" v-on:click="DownloadConfigFile"> Download Config File </v-btn>

      </v-container>

    </v-card-text>
  </v-card>
</template>

<script>
import utils from '../../utils';

export default {
  name: 'ExportSettings',

  props: {
    UnitSettings: {
      type: Object,
      default: () => {
        return {
          Alias: "Mission BUC",
          PowerOnState: false,
          PowerUnit: 0
        };
      }
    },

    FaultDefinitions: {
      type: Object,
      default: () => {
        return {
          HighRfPower: 41.2,
          HighRfOption: 2,

          LowRfPower: 21.5,
          LowRfOption: 2,

          ReflectedPower: 35.6,
          ReflectedOption: 2,
        }
      }
    },

    NtpOptions: {
      type: Object,
      default: () => {
        return {
          PrimaryAddr: "192.168.100.10",
          SecondaryAddr: "192.168.200.10",
          IsManual: true,
          ManualDateTime: "2021-01-12T12:30:00",
          UpdateDaily: false,
          UtcOffsetMinute: 80,
        }
      }
    },

    SerialOptions: {
      type: Object,
      default: () => {
        return {
          IsRs485: true,
          SelectedBaudrate: 115200
        }
      }
    },

    AlcOptions: {
      type: Object,
      default: () => {
        return {
          SetPoint: 32.1,
          HighAlarm: 30.0,
          LowAlarm: 20.2,
          HighFault: 40.0,
          LowFault: 40.0,
        };
      }
    },

    IpConfigurations: {
      type: Object,
      default: () => {
        return {
          IsDhcp: true,
          Address: "192.168.1.100",
          Subnet: "255.255.255.0",
          Gateway: "192.168.1.1"
        }
      },
    }
  },

  data() {
    return {
      dialog: false,
    }
  },

  computed: {
    GetPowerUnit() {
      return utils.GetPowerUnit(this.UnitSettings.PowerUnit);
    },
  },

  methods: {
    DownloadConfigFile() {
      const filename = `MissionConfig_${Date.now()}.json`;
      const contentType = "text/json";

      const filecontent = {
        UnitSettings: this.UnitSettings,
        FaultDefinitions: this.FaultDefinitions,
        NtpOptions: this.NtpOptions,
        SerialOptions: this.SerialOptions,
        AlcOptions: this.AlcOptions,
        IpConfigurations: this.IpConfigurations
      };
      
      delete filecontent.IpConfigurations.MAC;
      delete filecontent.SerialOptions.BaudrateOptions;

      this.SaveToFile(filename, JSON.stringify(filecontent, null, 2), contentType);
    },

    SaveToFile(filename, content, contentType) {
      const a = document.createElement('a');
      const file = new Blob([content], {type: contentType});

      a.href = URL.createObjectURL(file);
      a.download = filename;
      a.click();

      URL.revokeObjectURL(a.href);
    },

    GetFaultDefinition(selection) {
      return utils.GetFaultDefinition(selection);
    }
  },

  watch: {
    UnitSettings: function(value) {
      console.log("Watching UnitSettings", value);
    }
  }
}

</script>